*
{
    margin: 0;
    padding: 0;
    font-family: 'Courier New', Courier, monospace;
    letter-spacing: 1px;
    font-style: Roboto;

}

.container
{
    width: 100%;
    height: 100vh;
    display: flex;
}

.left_cotainer
{
    width: 30%;
    height: 100vh;
    background: linear-gradient(169.47deg, rgba(4, 31, 27, 0.98) 14.76%, #380C4A 25.62%, #3B0E46 29.25%, #3D0F42 33.37%, #441436 40.15%, #33254E 46.18%, #2B2D59 52.47%, #292654 57.59%, #271F4F 62.33%, #240D37 74.06%, #22052D 88.22%, #22052D 125.39%, #22052D 154.65%, #22052D 154.67%),
radial-gradient(26.86% 9.25% at 52.64% 24.47%, rgba(78, 26, 69, 0.2) 0%, rgba(69, 20, 43, 0.2) 14.29%, rgba(69, 20, 43, 0.2) 28.57%, rgba(61, 14, 18, 0.2) 42.86%, rgba(61, 13, 15, 0.2) 57.14%, rgba(61, 13, 15, 0.2) 71.43%, rgba(61, 13, 15, 0.2) 85.71%, rgba(60, 13, 13, 0.2) 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;

}
.right-container
{
    width: 70%;
    height: 100vh;
    background-color:#D9D9D9;

}
.card-container
{
    position: absolute;
    top:10%;
    left: 8%;
    height: 250px;
    width: 450px;
    background: linear-gradient(169.47deg, rgba(16, 117, 102, 0.98) 14.76%, #7c249e 25.62%, #8d3599 29.25%, #5fcf6e 33.37%, #441436 40.15%, #33254E 46.18%, #e444d6 52.47%, #292654 57.59%, #271F4F 62.33%, #240D37 74.06%, #22052D 88.22%, #22052D 125.39%, #22052D 154.65%, #22052D 154.67%),
    radial-gradient(26.86% 9.25% at 52.64% 24.47%, rgba(121, 64, 111, 0.2) 0%, rgba(82, 46, 165, 0.2) 14.29%, rgba(69, 20, 43, 0.2) 28.57%, rgba(61, 14, 18, 0.2) 42.86%, rgba(61, 13, 15, 0.2) 57.14%, rgba(61, 13, 15, 0.2) 71.43%, rgba(61, 13, 15, 0.2) 85.71%, rgba(172, 74, 74, 0.2) 100%) ;
    border-radius: 10px;
}
.card-back-container
{
    position: absolute;
    top:55%;
    left: 15%;
    height: 250px;
    width: 450px;
    background-color: rgb(155, 25, 155);
    border-radius: 10px;
}
.expire
{
    display: flex;
    justify-content: space-between;
}
.form-container
{
    position: relative;
    top:100px;
    left:400px;
}
.card
{
    margin-top: 20px;
    width: 400px;
}

.text_area
{
    height: 44px;
    width: 390px;
    border-radius: 6px;
    margin-top: 5px;
    border-style: none;
    padding-left: 7px;
    font-size: 15px;
    font-weight: 600;
    font-style: Inter;
}

.expire_box
{
    width: 75px;
    height: 44px;
    border-radius: 6px;
    margin-top: 5px;
    border-style: none;
    padding-left: 7px;
    font-size: 15px;
    font-weight: 600;
    font-style: Inter;
   
}

.cvv_textarea
{
    width: 178px;
    height: 44px;
    margin-top: 5px;
    border-radius: 6px;
    border-style: none;
    padding-left: 7px;
    font-size: 15px;
    font-weight: 600;
    font-style: Inter;
    
}


#submit_btn
{
    margin-top: 30px;
    border-radius: 6px;
    height: 44px;
    width: 400px;
    background-color: #220930;
    color: white;
    font-size: 16px;
}

.error_text
{
    color: red;
    margin-top: 5px;
    display: none;
}


/*--------------------------------------card front  --------------- */

.big_circle
{
    position: absolute;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: rgb(255, 255, 255);
    top:30px;
    left: 30px;
}

.small_circle
{
    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 2px solid wheat;
    top:55px;
    left: 120px;

}
.circle_container
{
    display: flex;
}

.card_num_container
{
    position: absolute;
    top:60%;
    left: 7%;
}
.card_num_container>p{
    font-size: 30px;
    color: wheat;
    font-weight: 600;
}
.card_name{
    width: 250px;
    height: 50px;
    
}
.card_name_exp
{
    position: absolute;
    top: 70%;
    left:4%;
}
.card_name_exp
{
  
    display: flex;
    padding: 20px;
    justify-content: space-between;
    color: wheat;
    font-family: 'Times New Roman', Times, serif;
    font-weight: 600;
   
}
.card_ex
{
    margin-left: 50px;
}


/* --------------------------------card-back-container--------------------------- */
.card-back-container
{
    background:#D9D9D9;
    border: 1px solid rgb(104, 83, 83);
}

.black_box
{
    width: 100%;
    height: 55px;
    background-color: black;
    top: 35px;
    position: relative;
}
.smallbox
{
position: relative;
width: 60%;
height: 35px;
margin-top: 45px;
left: 100px;
border-radius: 4px;
border: 1px;
 background: silver;
 display: flex;
 justify-content: end;
 align-items: center;
 padding-right: 5px;
 color: black;
 font-weight: 600;
}

#expire_year
{
    margin-left: 5px;
}
